html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*, *:before, *:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: Gilroy;
  overflow-x: hidden;
  position: relative;
  background-color: #000000;
  color: #ffffff;
  scroll-behavior: smooth;
}

a {
  color: #000000;
  transition: color 0.3s ease-in-out;
  text-decoration: none;
}

a:hover {
  color: #ffffff;
}

strong {
  font-weight: 700;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  width: 100%;
  align-items: center;
  padding: 2% 5%;
  margin: 0 auto;
  z-index: 9998;
  background-color: transparent;
}

@media screen and (max-width: 600px) {
  .header {
    flex-direction: column;
  }
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  overflow: hidden;
  height: calc(1rem + 4.5vh);
}

.logo__link svg,
.logo__link img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media screen and (max-width: 768px) {
  .logo__link {
    height: calc(1rem + 3.5vh);
  }
}

.navigation {
  width: 30%;
  height: 100vh;
  min-height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  background-color: #ff4f00;
  visibility: hidden;
  opacity: 0;
  transform: translateX(100%);
  z-index: 9997;
  transition: all 0.3s ease-in-out;
}

.navigation--active {
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
  transition: all 0.3s ease-in-out;
}

.navigation__inner {
  display: flex;
  width: 100%;
  padding: 10vh 0;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
}

.navigation__item {
  display: flex;
  width: 50%;
  justify-content: center;
  align-items: center;
}

.navigation__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 1rem;
  font-size: calc(0.25rem + 1vw);
  line-height: calc(0.25rem + 1.5vw);
}

.menu {
  display: flex;
  width: 4rem;
  height: 3rem;
  margin-left: 3rem;
  z-index: 9999;
  justify-content: center;
  align-items: center;
}

.menu:hover {
  cursor: pointer;
}

.menu:hover .menu__item::after,
.menu:hover .menu__item::before {
  background-color: white;
  width: 100%;
}

.menu--active:hover .menu__item::after,
.menu--active:hover .menu__item::before {
  width: 90%;
}

.menu--active .menu__item::after,
.menu--active .menu__item::before {
  background-color: white;
  width: 90%;
  transform-origin: center;
}

.menu--active .menu__item::after {
  transform: rotateZ(45deg) translateY(50%);
  bottom: 50%;
}

.menu--active .menu__item::before {
  transform: rotateZ(-45deg) translateY(-50%);
  top: 50%;
}

.menu__item {
  display: block;
  width: 100%;
  height: 3px;
  padding: 0.5rem 0;
  position: relative;
}

.menu__item::after, .menu__item::before {
  content: '';
  display: block;
  right: 0;
  position: absolute;
  background-color: #ff4f00;
  height: 3px;
  transition: all 0.3s ease-in-out;
}

.menu__item::before {
  top: 0;
  width: 100%;
}

.menu__item::after {
  bottom: 0;
  width: 80%;
}

.search-form {
  display: flex;
  margin-left: auto;
  position: relative;
  height: 4rem;
  align-items: center;
  justify-content: center;
}

.search-form__input {
  box-sizing: border-box;
  width: 2rem;
  height: 2rem;
  border: 2px solid #ff4f00;
  border-radius: 50%;
  background: none;
  color: #fff;
  font-size: 1rem;
  outline: 0;
  transition: width 0.3s ease-in-out, border-radius 0.5s ease-in-out, padding 0.2s;
  transition-delay: 0.4s;
}

.search-form__btn {
  background: none;
  position: absolute;
  top: 50%;
  right: 0;
  height: 100%;
  width: 100%;
  padding: 0;
  border-radius: 100%;
  outline: 0;
  border: 0;
  color: inherit;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  transform: translate(0, -50%);
}

.search-form:before {
  content: "";
  position: absolute;
  width: 15px;
  height: 3px;
  background-color: #ff4f00;
  transform: translate(50%, -350%) rotate(45deg);
  bottom: 0;
  right: 0;
  transition: all 0.3s ease-in-out;
}

.search-form--active {
  animation-name: in-out;
  animation-duration: 0.5s;
  animation-timing-function: linear;
}

.search-form--active::before {
  top: 50%;
  right: 50%;
  width: 50%;
  height: 2px;
  transform: translate(50%, -50%) rotate(45deg);
  display: none;
}

.close {
  transition: all 0.3s ease-in-out;
  transition-delay: 0.4s;
  width: 25%;
}

.close:before {
  content: "";
  position: absolute;
  top: 50%;
  width: 25px;
  height: 2px;
  margin-top: 0;
  margin-left: -10px;
  background-color: #ff4f00;
  transform: rotate(45deg);
  transition: all 0.3s ease-in-out;
}

.close:after {
  content: "";
  position: absolute;
  width: 25px;
  height: 2px;
  background-color: #ff4f00;
  margin-top: 0;
  margin-left: -10px;
  cursor: pointer;
  transform: rotate(-45deg);
}

.square {
  box-sizing: border-box;
  padding: 0 40px 0 10px;
  width: 12.5rem;
  height: 2.5rem;
  border: 2px solid #ff4f00;
  border-radius: 0;
  background: none;
  color: #fff;
  font-size: 1rem;
  outline: 0;
  transition: width 0.3s ease-in-out, border-radius 0.25s ease-in-out, padding 0.2s;
  transition-delay: 0.4s, 0s, 0.4s;
  transform: translate(0, 0%);
}

@media screen and (max-width: 1366px) {
  .navigation {
    width: 35vw;
  }
  .navigation__link {
    font-size: calc(0.25rem + 1.5vw);
    line-height: calc(0.25rem + 1.75vw);
  }
}

@media screen and (max-width: 1024px) {
  .navigation {
    width: 50vw;
  }
  .navigation__link {
    font-size: calc(0.25rem + 2vw);
    line-height: calc(0.25rem + 2vw);
  }
}

@media screen and (max-width: 768px) {
  .navigation__inner {
    padding: 35% 0;
  }
}

@media screen and (max-width: 600px) {
  .navigation {
    width: 100vw;
  }
  .navigation__link {
    font-size: calc(0.5rem + 3.5vw);
    line-height: calc(0.5rem + 3.5vw);
  }
  .menu {
    margin: 0 auto;
  }
  .menu__item::after, .menu__item::before {
    width: 100%;
  }
  .search-form {
    display: none;
  }
}

.slider {
  display: flex;
  width: 100%;
  min-height: 100vh;
  height: 100vh;
  background-color: #ff4f00;
  position: relative;
  overflow: hidden;
}

.slider__item {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  position: absolute;
  width: 100%;
  height: 100%;
  left: -100vw;
}

.slider-indicators {
  position: absolute;
  top: 50%;
  right: 5%;
  transform: translateY(-50%);
  width: 3.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  text-align: right;
}

.slider-indicators__item {
  width: 50%;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.65);
  margin: 0.5rem 0;
  transition: all 0.3s ease-in-out;
}

.slider-indicators__item:hover {
  cursor: pointer;
  background-color: #ff4f00;
}

.slider-indicators__item--active {
  height: 4px;
  width: 100%;
  background-color: #ff4f00;
}

.slide__img {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.slide__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slider-gallery {
  position: absolute;
  right: 5%;
  bottom: 5%;
  width: 50vw;
  background-color: blue;
  display: flex;
}

.slider-gallery__item {
  display: flex;
  width: 100%;
  height: 20vh;
}

.slider-gallery__item:hover {
  cursor: pointer;
}

.slider-gallery__item--active {
  border-top: 7.5px solid #ff4f00;
}

.slider-gallery__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero__content {
  display: flex;
  flex-direction: column;
  padding: 12.5vh 25% 0 12.5%;
  margin: 0 auto 15vh;
  background-color: #000000;
}

.hero__title {
  display: flex;
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 10px;
  font-size: calc(0.5rem + 0.5vw);
  line-height: calc(0.5rem + 0.5vw);
}

.hero__caption {
  width: 100%;
  padding-right: 15%;
  margin: calc(0.75rem + 0.75vw) 0;
  font-size: calc(0.5rem + 2.75vw);
  line-height: calc(0.5rem + 3vw);
  position: relative;
}

.hero__caption::before {
  content: '';
  display: block;
  width: 7.5px;
  height: 100%;
  background-color: #ff4f00;
  top: 0;
  left: -10%;
  position: absolute;
}

.hero__description {
  width: 100%;
  text-align: justify;
  font-size: calc(0.25rem + 0.9vw);
  line-height: calc(0.25rem + 1.5vw);
}

.color {
  color: #ff4f00;
  margin-left: 0.5rem;
}

.bold {
  font-weight: 800;
}

.btn {
  font-size: calc(0.25rem + 0.65vw);
  line-height: calc(0.25rem + 0.75vw);
  width: 40%;
  margin: 1rem 0;
  color: #ffffff;
  background-color: #ff4f00;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border: none;
  padding: 1rem 1.5rem;
  text-transform: uppercase;
  letter-spacing: 7.5px;
  transition: all 0.3s ease-in-out;
}

.btn:hover {
  box-shadow: inset 0 0 50px 5px #ff4f00;
  background-color: rgba(255, 79, 0, 0);
  cursor: pointer;
}

.slider-arrows {
  position: absolute;
  top: 65%;
  right: 40%;
  width: 13rem;
  height: 3.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transform: translate(50%, 0);
}

.slider-arrows__item {
  width: 50%;
  height: 100%;
}

.slider-arrows__item:hover svg {
  fill: #ff4f00;
}

.slider-arrows__item svg {
  height: 100%;
  width: 100%;
  object-fit: cover;
  fill: rgba(255, 255, 255, 0.6);
  transition: all 0.3s ease-in-out;
}

.slider-arrows--next {
  transform: translateX(100%);
}

.more {
  display: none;
}

.more--active {
  display: inline;
}

.scroll-down {
  display: none;
}

@media screen and (max-width: 1600px) {
  .hero__title {
    font-size: calc(0.25rem + 0.5vw);
    line-height: calc(0.25rem + 0.5vw);
  }
  .hero__caption {
    font-size: calc(0.25rem + 3vw);
    line-height: calc(0.25rem + 3.5vw);
  }
  .hero__description {
    font-size: calc(0.25rem + 1vw);
    line-height: calc(0.25rem + 1.35vw);
  }
  .btn {
    font-size: calc(0.25rem + 0.5vw);
    line-height: calc(0.25rem + 0.5vw);
  }
  .slider-arrows {
    right: 50%;
    transform: translate(142.5%, 0);
  }
}

@media screen and (max-width: 1366px) {
  .slider-arrows {
    right: 50%;
    transform: translate(135%, 0);
  }
  .slider-gallery {
    width: 60vw;
  }
  .slider-gallery__item {
    height: 15vh;
  }
}

@media screen and (max-width: 1224px) {
  .slider-arrows {
    right: 50%;
    transform: translate(130%, 0);
  }
}

@media screen and (max-width: 1024px) {
  .hero__title {
    font-size: calc(0.25rem + 0.75vw);
    line-height: calc(0.25rem + 0.75vw);
  }
  .hero__caption {
    font-size: calc(0.25rem + 3.25vw);
    line-height: calc(0.25rem + 3.75vw);
  }
  .hero__description {
    font-size: calc(0.25rem + 1.25vw);
    line-height: calc(0.25rem + 1.75vw);
  }
  .btn {
    font-size: calc(0.25rem + 0.5vw);
    line-height: calc(0.25rem + 0.5vw);
  }
  .slider-arrows {
    right: 50%;
    transform: translate(125%, 0);
  }
  .slider-gallery {
    width: 65vw;
  }
}

@media screen and (max-width: 768px) {
  .slider {
    height: auto;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  .slider__item {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
  .slider-gallery {
    display: none;
  }
  .hero__content {
    margin: 0 auto;
    padding: 10% 12.5%;
  }
  .hero__title {
    font-size: calc(0.25rem + 1.5vw);
    line-height: calc(0.25rem + 1.5vw);
  }
  .hero__caption {
    margin: calc(1rem + 1.25vw) auto;
    font-size: calc(0.25rem + 5vw);
    line-height: calc(0.25rem + 5.5vw);
  }
  .hero__caption::before {
    left: -7.5%;
  }
  .hero__description {
    font-size: calc(0.25rem + 1.75vw);
    line-height: calc(0.25rem + 2.5vw);
  }
  .slider-arrows {
    top: 60%;
    right: 10%;
    transform: translate(0, 0);
    width: 25vw;
  }
  .btn {
    margin: calc(1rem + 1vh) 0;
    font-size: calc(0.25rem + 1.25vw);
    line-height: calc(0.25rem + 1.5vw);
  }
}

@media screen and (max-width: 600px) {
  .slider-arrows {
    top: 90%;
    right: 50%;
    transform: translate(50%, 0);
    width: 40vw;
  }
  .hero__content {
    padding: 10% 7.5%;
  }
  .hero__title {
    font-size: calc(0.25rem + 1.5vw);
    line-height: calc(0.25rem + 1.5vw);
  }
  .hero__caption {
    margin: calc(1rem + 1.25vw) auto;
    font-size: calc(0.5rem + 5.75vw);
    line-height: calc(0.5rem + 6vw);
  }
  .hero__caption::before {
    left: -5%;
  }
  .hero__description {
    font-size: calc(0.25rem + 2.25vw);
    line-height: calc(0.25rem + 3vw);
  }
}

@media screen and (max-width: 480px) {
  @keyframes bounce {
    0% {
      top: 10%;
    }
    100% {
      top: 50%;
      height: 40%;
    }
  }
  .slider {
    height: 200vh;
  }
  .slider-arrows {
    width: 100vw;
    top: 92.5%;
  }
  .slider-indicators {
    display: none;
    position: fixed;
    top: 60%;
  }
  .hero__content {
    padding: 25% 7.5%;
    justify-content: space-evenly;
  }
  .hero__title {
    font-size: calc(0.25rem + 1.5vw);
    line-height: calc(0.5rem + 1.5vw);
  }
  .hero__caption {
    margin: calc(1rem + 1vw) auto;
    font-size: calc(0.5rem + 7vw);
    line-height: calc(0.5rem + 7.5vw);
  }
  .hero__caption::before {
    width: 5px;
  }
  .hero__description {
    font-size: calc(0.5rem + 2.25vw);
    line-height: calc(0.5rem + 3vw);
  }
  .btn {
    margin: calc(1rem + 1vh) 0;
    font-size: calc(0.35rem + 1vw);
    line-height: calc(0.35rem + 1.25vw);
  }
  .scroll-down {
    display: flex;
    justify-content: center;
    text-align: center;
    transform: translate(-50%, -50%);
    position: absolute;
    top: 45%;
    left: 50%;
    width: 3rem;
    height: 5rem;
    border-radius: 20px;
    border: 2px solid #ff4f00;
  }
  .scroll-down__bounce {
    position: relative;
    width: 40%;
    height: 30%;
    border-radius: 20px;
    transition: all 0.3s ease-in-out;
    background-color: rgba(255, 79, 0, 0.75);
    animation: bounce 2s ease-in-out alternate-reverse infinite;
  }
}

